"use client"

import Link from "next/link"
import Image from "next/image"
import { usePathname } from "next/navigation"
import { translations } from "../i18n/translations"

export default function Header({ language, setLanguage }) {
  const pathname = usePathname()
  const t = translations[language].nav

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId)
    if (element) {
      const headerOffset = 100
      const elementPosition = element.getBoundingClientRect().top
      const offsetPosition = elementPosition + window.pageYOffset - headerOffset

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      })
    }
  }

  const handleSectionClick = (e, sectionId) => {
    if (pathname === '/') {
      e.preventDefault()
      scrollToSection(sectionId)
    }
  }

  return (
    <header className="border-b-4 border-foreground sticky top-0 z-50 relative">
      {/* Background image */}
      <div className="absolute inset-0 z-0">
        <Image
          src="/assets/header1.png"
          alt="Header background"
          fill
          className="object-cover"
          priority
        />
      </div>

      {/* Content */}
      <div className="relative z-10 max-w-6xl mx-auto px-4 py-6 flex justify-center">
        <div className="sketch-box bg-background inline-block">
          <nav className="flex items-center divide-x-3 divide-foreground">
            <div className="px-8 py-4 text-2xl font-black flex items-center gap-2">
              âœ¦ Jihan Aqilah Hartono
            </div>
            <Link 
              href="/" 
              className="px-8 py-4 text-base font-bold hover:text-accent transition"
            >
              {t.home}
            </Link>
            <Link 
              href="/#about"
              onClick={(e) => handleSectionClick(e, 'about')}
              className="px-8 py-4 text-base font-bold hover:text-accent transition"
            >
              {t.about}
            </Link>
            <Link 
              href="/portfolio" 
              className="px-8 py-4 text-base font-bold hover:text-accent transition"
            >
              {t.portfolio}
            </Link>
            <Link 
              href="/#journey"
              onClick={(e) => handleSectionClick(e, 'journey')}
              className="px-8 py-4 text-base font-bold hover:text-accent transition"
            >
              {t.journey}
            </Link>
            
            {/* Language Switcher */}
            <div className="flex items-center gap-2 px-4 py-4">
              <button
                onClick={() => setLanguage('id')}
                className={`px-3 py-1 text-sm font-bold rounded transition ${
                  language === 'id' 
                    ? 'bg-accent text-background' 
                    : 'hover:bg-accent/20'
                }`}
              >
                ID
              </button>
              <button
                onClick={() => setLanguage('en')}
                className={`px-3 py-1 text-sm font-bold rounded transition ${
                  language === 'en' 
                    ? 'bg-accent text-background' 
                    : 'hover:bg-accent/20'
                }`}
              >
                EN
              </button>
            </div>
          </nav>
        </div>
      </div>
    </header>
  )
}